<template>
  <div class="user mescroll">
    <van-cell-group :border="false" style="padding-top: 10px;">
      <van-cell :border="false" class="upload">
        <van-uploader :after-read="onRead" accept="image/*" disabled>
          <div class="pic">
            <img :src="$utils.dealImg(pic) || require('@/assets/img/default/userDefault.png')" alt />
            <!-- <p>修改头像</p> -->
          </div>
        </van-uploader>
      </van-cell>
      <van-field :border="false" v-model="name" input-align="right" clearable label="昵称" placeholder="请填写" @blur="blur()" disabled />
      <van-field :border="false" v-model="tel" input-align="right" clearable label="联系方式" placeholder="请填写" @blur="blur()" disabled />
      <van-cell :border="false" title="性别" :value="sex?sex=='1'?'男':'女':'请选择'" is-link @click="sexShow = true" />
      <van-cell :border="false" title="所在地" :value="address|| '请选择'" is-link @click="showAddress = true" v-if="false" />
    </van-cell-group>
    <van-cell-group :border="false" v-if="false">
      <div class="line"></div>
      <van-cell :border="false" title="我的兴趣" :value="interest || '请选择'" is-link @click="toInterest()" />
      <van-field :border="false" v-model="signature" input-align="right" clearable label="个性签名" placeholder="请填写" @blur="blur()" />
    </van-cell-group>
    <van-action-sheet v-model="sexShow" :actions="sexActions" cancel-text="取消" @select="onSelect" @cancel="onCancel" />
    <van-popup v-model="showAddress" position="bottom">
      <van-area :area-list="areaList" :value="cityId" :columns-num="2" @confirm="areaConfirm" @cancel="showAddress = false" />
    </van-popup>
    <!-- <div class="btn" @click="submit">提交</div> -->
  </div>
</template>
<script>
import {
  Field,
  Cell,
  CellGroup,
  Uploader,
  ActionSheet,
  Popup,
  Area
} from "vant";
import areaList from "../../utils/AreaList";
import { getPersonalInfo, editPersonalInfo } from "@/api";
export default {
  components: {
    [Field.name]: Field,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Uploader.name]: Uploader,
    [ActionSheet.name]: ActionSheet,
    [Popup.name]: Popup,
    [Area.name]: Area
  },
  data() {
    return {
      pic: "",
      name: "",
      tel: "",
      sex: "1",
      sexShow: false,
      sexActions: [
        {
          name: "男",
          value: "1"
        },
        {
          name: "女",
          value: "2"
        }
      ],
      showAddress: false,
      areaList: {},
      address: "江苏省 无锡市",
      provinceId: "",
      cityId: "320200",
      interest: "",
      signature: ""
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    onRead(file) {
      console.log(file);
      let formData = new FormData();
      formData.append("file", file.file);
      formData.append("path", 1);
      let config = {
        headers: { "Content-Type": "multipart/form-data" }
      };
      const loading = this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: "上传中..."
      });
      // 走上传接口
      console.log(formData);
      this.$axios
        .post(
          "http://wxoa.wxjggl.com/govOA/app/sys/user/updateFiles?userId=" +
            sessionStorage.getItem("userId"),
          formData,
          config
        )
        .then(res => {
          loading.clear();
          console.log(res);
          if (res.success == 1) {
            this.pic = this.$utils.dealImg(res.data.file);
          } else {
            this.$toast.fail("图片上传失败！");
          }
        })
        .catch(err => {
          this.$toast.clear();
          console.log(err);
          this.$toast.fail("网络错误");
        });
    },
    onSelect(item) {
      this.sex = item.value;
      this.sexShow = false;
      this.submit();
    },
    onCancel() {
      this.sexShow = false;
    },
    areaConfirm(obj) {
      this.showAddress = false;
      this.address = `${obj[0].name} ${obj[1].name}`;
      this.provinceId = obj[0].code;
      this.cityId = obj[1].code;
    },
    toInterest() {
      this.$router.push({
        name: "interest"
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getPersonalInfo({
        token:
          sessionStorage.getItem("token") || "177128e71cd2d4bc4acdba48bcae04709"
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.pic = res.data.head_portrait;
          this.name = res.data.user_name;
          this.tel = res.data.phone;
          this.sex = res.data.gender;
        }
      });
    },
    submit() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      let dataObj = {
        token:
          sessionStorage.getItem("toekn") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        gender: this.sex
      };
      editPersonalInfo(dataObj).then(res => {
        loading.clear();
      });
    }
  },
  mounted() {
    document.title = "个人资料";
    this.areaList = areaList;
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.user {
  background: #fff;
  .pic {
    // width: 30px;
    height: 30px;
    line-height: 30px;
    overflow: hidden;
    display: flex;
    img {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      margin-right: 5px;
    }
  }
  .line {
    border-top: 1pt solid #e8e9e9;
    margin: 15px;
  }
  .btn {
    width: 90%;
    margin: 30px auto 0;
    text-align: center;
    background: $base-color;
    color: white;
    height: 50px;
    line-height: 50px;
    // border-radius: 10px;
    font-size: 18px;
    margin-bottom: 30px;
  }
}
</style>
<style lang="scss">
.user {
  .van-cell-group {
    padding: 0 15px;
  }
  .van-cell {
    align-items: center;
    padding: 10px 0;
    background: #fff;
  }
  input::-webkit-input-placeholder {
    color: #999;
  }
  .van-cell__value {
    color: #999;
    // height: 30px;
  }
  .upload {
    .van-cell__value {
      height: 30px;
    }
  }
  .van-field__control:disabled {
    color: #999;
  }
  .van-switch--on {
    background: $base-color;
  }
  .van-nav-bar {
    background-color: $base-color;
    color: #fff;
  }
  .van-nav-bar__arrow {
    color: #fff !important;
  }
  .van-nav-bar__text {
    color: #fff;
  }
  .van-uploader {
    width: 100%;
  }
}
</style>